import Vue from 'vue'
import Router from 'vue-router'
import UserLogin from '@/components/UserLogin'
import Index from '@/components/Index'
import Home from '@/components/Home'
import UserList from '@/components/UserList'
import ShopList from '@/components/ShopList'
import FoodList from '@/components/FoodList'
import OrderList from '@/components/OrderList'
import AdminList from '@/components/AdminList'
import addShop from '@/components/AddShop'
import addFood from '@/components/AddFood'
import AdminSetUp from '@/components/AdminSetUp'
import Explain from '@/components/Explain'
import UserDis from '@/components/UserDis'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: UserLogin
    },
    {
      path: '/manage',
      name: 'Manage',
      component: Index,
      children: [
        {
          path: '/',
          name: 'Home',
          component: Home,
        },
        {
          path: '/UserList',
          name: 'UserList',
          component: UserList,
          meta: ['数据管理', '用户列表'],
        },
        {
          path: '/ShopList',
          name: 'ShopList',
          component: ShopList,
          meta: ['数据管理', '商家列表'],
        },
        {
          path: '/FoodList',
          name: 'FoodList',
          component: FoodList,
          meta: ['数据管理', '食品列表'],
        },
        {
          path: '/OrderList',
          name: 'OrderList',
          component: OrderList,
          meta: ['数据管理', '订单列表'],
        },
        {
          path: '/AdminList',
          name: 'AdminList',
          component: AdminList,
          meta: ['数据管理', '管理员列表'],
        },
        {
          path: '/addShop',
          name: 'addShop',
          component: addShop,
          meta: ['添加数据', '添加商铺'],
        },
        {
          path: '/addFood',
          name: 'addFood',
          component: addFood,
          meta: ['添加数据', '添加商品'],
        },
        {
          path: '/AdminSetUp',
          name: 'AdminSetUp',
          component: AdminSetUp,
          meta: ['设置', '管理员设置'],
        },
        {
          path: '/Explain',
          name: 'Explain',
          component: Explain,
          meta: ['说明', '说明'],
        },
        {
          path: '/UserDis',
          name: 'UserDis',
          component: UserDis,
          meta: ['图表', '用户分布'],
        },
      ]
    }
  ]
})



