import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state:{
    login:{},
    formID: '',
  },
  getters:{},
  mutations:{
    updateLoginApp(state,data){
      state.login = data;
    },
    addFood(state, item){
      state.formID = item;
    }
  },
  actions:{
    loginApp(store,item){
      fetch('http://cangdu.org:8001/admin/login',{
        method: "POST",
        body: JSON.stringify(item),
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        credentials: 'include',
        cache: "no-cache",
        mode: "cors",
      }).then( response => {
        return response.json();
      }).then( data =>{
        store.commit('updateLoginApp',data);
      }).catch(function(error) {
        console.log("Oops, error",error);
      });
    },
    handleCommand(store,item){
      fetch('http://cangdu.org:8001/admin/singout')
        .then( response => {
          return response.json();
        }).then( data =>{
          console.log(data)
        store.commit('updateHandleCommand',data);
      }).catch(function(error) {
        console.log("Oops, error",error);
      });
    },
  }
})

export default store;



